@import "styles/variables.scss";

.authContainer {
  @include flexCenter;
  flex-direction: column;
  height: 100vh;

  .authBtns {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 320px;

    .authBtn {
      cursor: pointer;
      border-radius: 20px;
      border: none;
      padding: 10px 0px;
      font-size: 12px;
      text-align: center;
      width: 150px;
      background: white;
    }
  }
}
